<template lang="pug">
component.material-icons.mdc-toolbar__icon(:is="tag", :href="link", :aria-label="label", :alt="label") {{ icon }}
</template>

<script>
export default {
  name: "MdcToolbarIcon",
  props: {
    link: String,
    label: String,
    icon: {
      type: String,
      required: true
    }
  },
  computed: {
    tag() {
      return this.link ? "a" : "button";
    }
  }
};
</script>