<template lang="pug">
li.mdc-grid-tile
  .mdc-grid-tile__primary
    .mdc-grid-tile__primary-content(v-if="cover", :style="coverStyle")
    img.mdc-grid-tile__primary-content(v-else :src="src")
  span.mdc-grid-tile__secondary(v-if="text || title")
    i.mdc-grid-tile__icon.material-icons(v-if="icon") {{ icon }}
    span.mdc-grid-tile__title(v-if="title") {{ title }}
    span.mdc-grid-tile__support-text(v-if="text") {{ text }}


</template>

<script>
export default {
  name: "MdcGridTile",
  props: {
    cover: Boolean,

    src: {
      type: String,
      required: true
    },
    title: String,
    text: String,
    icon: String
  },
  computed: {
    coverStyle() {
      return `background-image: url(${src})`;
    }
  }
};
</script>