const MdcLink = {
  functional: true,
  props: {
    tag: {
      type: String,
      default: "a"
    },
    link: String,
    to: String,
    replace: Boolean,
    append: Boolean,
    exact: Boolean
  },
  render(h, ctx) {
    const { data } = ctx;
    let props, tag = data.props.tag;

    if(data.props.to) {
      tag = "router-link";
      data.props.link = undefined;
    } else {
      if(tag === "a") {
        data.attrs = { href: data.props.link };
      }

      data.props = {};
    }

    return h(tag, data, ctx.children);
  }
};

export default function install(activeClass, exactActiveClass) {
  return {
    components: [ MdcLink ],
    props: {
      to: String,
      replace: Boolean,
      append: Boolean,
      exact: Boolean
    },
    computed: {
      $_link() {
        return this.to && {
          activeClass, exactActiveClass,
          to: this.to,
          replace: this.replace,
          append: this.append,
          exact: this.exact
        };
      }
    }
  };
}