<template lang="pug">
.mdc-form-field(:class="cssClasses")
  slot
  label {{label}}
</template>

<script>
export default {
  name: "MdcFormField",
  props: {
    label: {
      type: String,
      required: true
    },
    alignEnd: Boolean
  },
  computed: {
    cssClasses() {
      return {
        "mdc-form-field--align-end": this.alignEnd
      }
    }
  }
};
</script>